@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
    text-align: center;
    padding: 2% 5%;
}

.App * {
    box-sizing: border-box;
}

ul {
    padding: 0;
    list-style: none;
}

p {
    margin: 5px 0;
}

.display-none {
    display: none;
}
.showSuggestions {
    display: block;
}

input[type='text'],
input[type='number'] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
}

.city {
    width: 250px;
}

.CitySearch {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    max-width: 600px;
    margin: 40px auto 30px;
    position: relative;
}

label[for='city'],
label[for='number-of-events'] {
    margin-bottom: 10px;
    font-weight: 700;
}

.suggestions {
    width: 250px;
    position: absolute;
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 70px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
    background-color: #bcdcdb;
}

.Event {
    padding: 10px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    max-width: 2000px;
    margin-left: auto;
    margin-right: auto;
}

.show-details,
.hide-details {
    display: flex;
    margin: 15px 0 0 auto;
    padding: 5px 8px;
    outline: none;
    border-radius: 8px;
    cursor: pointer;
    background: #2f2f2f;
    border: solid 2px #2f2f2f;
    color: white;
}

.show-details:hover,
.hide-details:hover {
    background: rgba(47, 47, 47, 0.85);
}

