.about-event {
    margin-top: 5px;
}

.description {
    overflow-x: hidden;
    margin-top: 5px;
}

.NumberOfEvents {
    display: flex;
    flex-direction: column;
    width: 250px;
    margin: 0px auto 30px;
    text-align: center;
}

.OfflineAlert p {
    margin: 0 auto;
}

.data-vis-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 2000px;
    margin: 0 auto;
}
.data-vis-wrapper .recharts-responsive-container {
    min-width: 100%;
    display: flex;
}
.recharts-surface {
    overflow: visible;
}

